    .effect-8 ~ .focus-border:before,
    .effect-8 ~ .focus-border:after{content: ""; position: absolute; top: 0; left: 0; width: 0; height: 3.5px; background-color: rgb(111, 36, 141); transition: 0.3s;}
    .effect-8 ~ .focus-border:after{top: auto; bottom: 0; left: auto; right: 0;}
    .effect-8 ~ .focus-border i:before,
    .effect-8 ~ .focus-border i:after{content: ""; position: absolute; top: 0; left: 0; width: 3.5px; height: 0; background-color: rgb(111, 36, 141); transition: 0.4s;}
    .effect-8 ~ .focus-border i:after{left: auto; right: 0; top: auto; bottom: 0;}
    .effect-8:focus ~ .focus-border:before,
    .effect-8:focus ~ .focus-border:after{width: 100%; transition: 0.3s;}
    .effect-8:focus ~ .focus-border i:before,
    .effect-8:focus ~ .focus-border i:after{height: 100%; transition: 0.4s;}



