    padding-bottom: 0.7rem;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;

    width: 30%;
    margin: 0 auto;
}

.OneFullPost .commentInfo .addCommentBtn {
    box-sizing: border-box;
padding: 0.5rem 1rem;
background-color: rgb(111, 36, 141);

border: 2px solid transparent;
outline: none;

color: white;
border-radius: 1rem;

cursor: pointer;
transition: all 400ms ease;

font-weight: 700;
}

.OneFullPost .commentInfo .addCommentBtn:hover{
    background-color : transparent;
    color: rgb(111, 36, 141);
    border: 2px solid rgb(111, 36, 141);
    font-weight: 700;
}
